
import { Injectable } from '@angular/core';
import { ToastController, LoadingController, Platform, Loading, AlertController } from 'ionic-angular';
import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';
import { AppVersion } from '@ionic-native/app-version';
import { Camera, CameraOptions } from '@ionic-native/camera';
import { Toast } from '@ionic-native/toast';
import { File, FileEntry } from '@ionic-native/file';
import { Transfer, TransferObject } from '@ionic-native/transfer';
// import { InAppBrowser } from '@ionic-native/in-app-browser';
import { ImagePicker } from '@ionic-native/image-picker';
import { Network } from '@ionic-native/network';
import { Observable } from "rxjs";
import { Http, Response, RequestOptions, Headers } from "@angular/http";
// import { AppMinimize } from "@ionic-native/app-minimize";
import { DomSanitizer } from '@angular/platform-browser';
import { FileTransfer, FileUploadOptions, FileTransferObject } from '@ionic-native/file-transfer';
import { Position } from "../model/type";
import {
  APP_DOWNLOAD,
  APK_DOWNLOAD,
  IMAGE_SIZE,
  QUALITY_SIZE,
  REQUEST_TIMEOUT,
  APP_VERSION_SERVE_URL,
  APP_SERVE_URL
} from "./Constants";
import { Parameter } from "../model/Model";
import { Utils } from "./Utils";

declare var LocationPlugin;

// declare var cordova: any;
// declare var IntentShim;
declare var intentShim: any;

@Injectable()
export class NativeService {
  private loading: Loading;
  private loadingIsOpen: boolean = false;

  constructor(private platform: Platform,
    private toastCtrl: ToastController,
    private alertCtrl: AlertController,
    private statusBar: StatusBar,
    private splashScreen: SplashScreen,
    private appVersion: AppVersion,
    private camera: Camera,
    private toast: Toast,
    private transfer: Transfer,
    private file: File,
    // private inAppBrowser: InAppBrowser,
    private imagePicker: ImagePicker,
    private network: Network,
    // private appMinimize: AppMinimize,
    private loadingCtrl: LoadingController,
    private filetransfer: FileTransfer,
    public http: Http,
    public sanitizer: DomSanitizer) {
  }

  warn(info): void {
    console.log('%cNativeService/' + info, 'color:#e8c406');
  }


  /**
   * url
   */
  openUrlByBrowser(url: string): void {
    // this.inAppBrowser.create(url, '_system');
  }

  /**
 * app
 */
  detectionUpgrade(manual = false): void {
    if (this.isAndroid()) {
      //app
      this.getPackageName().subscribe(packageName => {
        // let appName = packageName.substring(packageName.lastIndexOf('.') + 1);
        // let appType = this.isAndroid() ? 'android' : 'ios';
        //app
        let url = APP_VERSION_SERVE_URL;
        this.http.get(url).map((res: Response) => res.json()).subscribe(res => {
          if (!res) {
            this.showToast("app");
            // this.logger.log('', 'app', { url: url });
            return;
          }
          //app
          this.getVersionNumber().subscribe(currentNo => {
            if (currentNo == res.version) {
              //
              // manual && this.alert('');
            } else {
              if (res.isForcedUpdate == 1) {//
                this.alertCtrl.create({
                  title: '',
                  subTitle: '',
                  buttons: [{
                    text: '',
                    handler: () => {
                      this.downloadApp();
                    }
                  }
                  ]
                }).present();
              } else {
                this.alertCtrl.create({
                  title: '',
                  subTitle: ',',
                  buttons: [{ text: '' },
                  {
                    text: '',
                    handler: () => {
                      this.downloadApp();
                    }
                  }
                  ]
                }).present();
              }

            }
          })
        });
      })
    }

  }

  /**
   * app
   */
  downloadApp(): void {
    if (this.isIos()) {//ios
      this.showToast("App Store");
      // this.openUrlByBrowser(APP_DOWNLOAD);
    }
    if (this.isAndroid()) {//android
      //
      let alert = this.alertCtrl.create({
        title: '0%',
        enableBackdropDismiss: false,
        buttons: ['']
      });
      alert.present();

      const fileTransfer: TransferObject = this.transfer.create();
      const apk = this.file.externalRootDirectory + 'sji.apk'; //apk

      //apk
      fileTransfer.download(APK_DOWNLOAD, apk).then(() => {
        window['install'].install(apk.replace('file://', ''));
      }, err => {
        alert.dismiss();
        // this.logger.log(err, 'android app ');
        // this.toastService.ShowToastText("android ");
        // this.alertCtrl.create({
        //   title: '',
        //   subTitle: '',
        //   buttons: [
        //     {
        //       text: '',
        //       handler: () => {
        //         this.openUrlByBrowser(APP_DOWNLOAD);//
        //       }
        //     }
        //   ]
        // }).present();
      });

      //
      fileTransfer.onProgress((event: ProgressEvent) => {
        let num = Math.floor(event.loaded / event.total * 100);
        if (num === 100) {
          alert.dismiss();
        } else {
          let title = document.getElementsByClassName('alert-title')[0];
          title && (title.innerHTML = '' + num + '%');
        }
      });
    }
  }

  /**
   * 
   * @return {boolean}
   */
  isMobile(): boolean {
    return this.platform.is('mobile') && !this.platform.is('mobileweb');
  }

  /**
   * android
   * @return {boolean}
   */
  isAndroid(): boolean {
    return this.isMobile() && this.platform.is('android');
  }

  /**
   * ios
   * @return {boolean}
   */
  isIos(): boolean {
    return this.isMobile() && (this.platform.is('ios') || this.platform.is('ipad') || this.platform.is('iphone'));
  }

  alert(title: string, subTitle: string = "", ): void {
    this.alertCtrl.create({
      title: title,
      subTitle: subTitle,
      buttons: [{ text: '' }]
    }).present();
  }

  /**
   * 
   * @param message 
   * @param duration 
   */
  showToast(message: string = '', duration: number = 2000): void {
    if (this.isMobile()) {
      this.toast.show(message, String(duration), 'center').subscribe();
    } else {
      this.toastCtrl.create({
        message: message,
        duration: duration,
        position: 'middle',
        showCloseButton: false
      }).present();
    }
  };


  /**
   * loading
   * @param content 
   */
  showLoading(content: string = ''): void {
    if (!this.loadingIsOpen) {
      this.loadingIsOpen = true;
      this.loading = this.loadingCtrl.create({
        content: content
      });
      this.loading.present();
      setTimeout(() => {//10
        this.loadingIsOpen && this.loading.dismiss();
        this.loadingIsOpen = false;
      }, 10000);
    }
  };



  /**
   * loading
   */
  hideLoading(): void {
    this.loadingIsOpen && this.loading.dismiss();
    this.loadingIsOpen = false;
  };

  /**
   * cordova-plugin-camera
   * @param options
   * @returns {Promise<string>}
   */
  getPicture(options: CameraOptions = {}): Observable<string> {
    let ops: CameraOptions = Object.assign({
      sourceType: this.camera.PictureSourceType.CAMERA,//,CAMERA:,PHOTOLIBRARY:
      destinationType: this.camera.DestinationType.DATA_URL,//base64,DATA_URL:base64   FILE_URI:
      quality: QUALITY_SIZE,//0 - 100
      allowEdit: false,//
      encodingType: this.camera.EncodingType.JPEG,
      targetWidth: IMAGE_SIZE,//
      targetHeight: IMAGE_SIZE,//
      saveToPhotoAlbum: false,//
      correctOrientation: true//
    }, options);
    return Observable.create(observer => {
      this.camera.getPicture(ops).then((imgData: string) => {
        if (ops.destinationType === this.camera.DestinationType.DATA_URL) {
          observer.next('data:image/jpg;base64,' + imgData);
        } else {
          observer.next(imgData);
        }
      }).catch(err => {
        if (err == 20) {
          this.alert(',');
          return;
        }
        if (String(err).indexOf('cancel') != -1) {
          return;
        }
        // this.logger.log(err, 'cordova-plugin-camera');
        this.alert('');
      });
    });
  };

  /**
   * 
   * @param options
   */
  getPictureByCamera(options: CameraOptions = {}): Observable<string> {
    let ops: CameraOptions = Object.assign({
      sourceType: this.camera.PictureSourceType.CAMERA,
      destinationType: this.camera.DestinationType.DATA_URL//DATA_URL: 0 base64, FILE_URI: 1
    }, options);
    return this.getPicture(ops);
  };

  /**
   * 
   * @param options
   */
  getPictureByPhotoLibrary(options: CameraOptions = {}): Observable<string> {
    let ops: CameraOptions = Object.assign({
      sourceType: this.camera.PictureSourceType.PHOTOLIBRARY,
      destinationType: this.camera.DestinationType.DATA_URL//DATA_URL: 0 base64, FILE_URI: 1
    }, options);
    return this.getPicture(ops);
  };

  /**
   * 
   * @param options
   */
  getMultiplePicture(options = {}): Observable<any> {
    let that = this;
    let ops = Object.assign({
      maximumImagesCount: 6,
      width: IMAGE_SIZE,//
      height: IMAGE_SIZE,//
      quality: QUALITY_SIZE//0 - 100
    }, options);
    return Observable.create(observer => {
      this.imagePicker.getPictures(ops).then(files => {
        let destinationType = options['destinationType'] || 0;//0:base64,1:url
        if (destinationType === 1) {
          observer.next(files);
        } else {
          let imgBase64s = [];//base64
          for (let fileUrl of files) {
            that.convertImgToBase64(fileUrl).subscribe(base64 => {
              imgBase64s.push(base64);
              if (imgBase64s.length === files.length) {
                observer.next(imgBase64s);
              }
            })
          }
        }
      }).catch(err => {
        // this.logger.log(err, '');
      });
    });
  };

  convertImgToBase64(path: string): Observable<string> {
    return Observable.create(observer => {
      this.file.resolveLocalFilesystemUrl(path).then((fileEnter: FileEntry) => {
        fileEnter.file(file => {
          let reader = new FileReader();
          reader.onloadend = function (e) {
            observer.next(this.result);
          };
          reader.readAsDataURL(file);
        });
      }).catch(err => {
        this.showToast("");
        // this.logger.log(err, 'base64');
      });
    });
  }
  private getFileContentAsBase64(path: string, callback) {
    function fail(err) {
      console.log('Cannot found requested file' + err);
    }

    function gotFile(fileEntry) {
      fileEntry.file(function (file) {
        let reader = new FileReader();
        reader.onloadend = function (e) {
          let content = this.result;
          callback(content);
        };
        reader.readAsDataURL(file);
      });
    }

    this.file.resolveLocalFilesystemUrl(path).then(fileEnter => gotFile(fileEnter)).catch(err => fail(err));
    // window['resolveLocalFileSystemURL'](path, gotFile, fail);
  }

  /**
   *  `unknown`, `ethernet`, `wifi`, `2g`, `3g`, `4g`, `cellular`, `none`
   */
  getNetworkType(): string {
    if (!this.isMobile()) {
      return 'wifi';
    }
    return this.network.type;
  }

  /**
   * 
   * @returns {boolean}
   */
  isConnecting(): boolean {
    return this.getNetworkType() != 'none';
  }

  /**
   * app,0.01
   * @description  /config.xmlversion
   */
  getVersionNumber(): Observable<string> {
    return Observable.create(observer => {
      this.appVersion.getVersionNumber().then((value: string) => {
        observer.next(value);
      }).catch(err => {
        this.showToast("app");
      });
    });
  }


  /**
   * app name,ionic2_tabs
   * @description  /config.xmlname
   * @returns {Promise<string>}
   */
  getAppName(): Promise<string> {
    return new Promise((resolve) => {
      this.appVersion.getAppName().then((value: string) => {
        resolve(value);
      }).catch(err => {
        this.warn('getAppName:' + err);
      });
    });
  }

  /**
   * app/id,com.kit.ionic2tabs
   * @description  /config.xmlid
   * @returns {Promise<string>}
   */
  getPackageName(): Observable<string> {
    return Observable.create(observer => {
      this.appVersion.getPackageName().then((value: string) => {
        observer.next(value);
      }).catch(err => {
        this.showToast("app");
      });
    });
  }

  /**
   * 
   */
  statusBarStyleDefault() {
    this.statusBar.styleDefault();
  }

  /**
   * 
   */
  splashScreenHide() {
    this.splashScreen.hide();
  }

  /**
   * app
   */
  minimize() {
    // this.appMinimize.minimize()
  }

  /**
   * 
   * @return {Promise<Position>}
   */
  getUserLocation(): Observable<Position> {
    return Observable.create(observer => {
      if (this.isMobile()) {
        LocationPlugin.getLocation(data => {
          observer.next({ 'lng': data.longitude, 'lat': data.latitude });
        }, msg => {
          observer.error('');
          if (msg.indexOf('') == 0) {
            this.alert('');
            return;
          }
          this.alert('' + msg);
          // this.logger.log(msg, '');
        });
      } else {
        console.log(',');
        observer.next({ 'lng': 118.55, 'lat': 36.28 });
      }
    });
  }





  /** */
  uploadByTransfer(fileUrl: string, url?: string, options?: FileUploadOptions): Promise<any> {
    var url = APP_SERVE_URL + "/MobService/UploadFile";
    if (!options) {
      options = {
        fileKey: 'file',
        fileName: fileUrl.substr(fileUrl.lastIndexOf('/') + 1)
      };
    }
    var transfer: FileTransferObject = this.filetransfer.create();
    return transfer.upload(fileUrl, url, options);
  }

  /** */
  public uploadFileByMutiTranser(filePaths: string[]): Promise<any> {
    let observabels: any[] = [];
    filePaths.forEach((value: string, i, array) => {
      if (!value.startsWith('file://')) {
        value = 'file://' + value;
      }
      let observabel = this.uploadByTransfer(value);
      observabels.push(observabel);
    });
    return Promise.all(observabels).then(res => {
      let result: any[] = [];
      res.forEach(value => {
        var msg = JSON.parse(value.response);
        if (msg.Code == "1") {
          result.push(msg.Msg);
        }
      });
      return result;
    });
  }

  public assembleHTML(strHTML: any) {
    return this.sanitizer.bypassSecurityTrustHtml(strHTML);
  }

  /** */
  public uploadWxFile(serverId): Observable<any> {
    var Info = {
      media_id: serverId
    }
    var parm = new Parameter();
    parm.jyh = "1131";
    parm.parm = JSON.stringify(Info);
    let headers = new Headers({
      'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8',
      'Accept': 'application/json;charset=utf-8'
    });
    let url = "";
    url = NativeService.replaceUrl("/MobService/Index");
    var options = new RequestOptions({ headers: headers });
    return this.http.post(url, NativeService.buildURLSearchParams(parm).toString(), options).map(res => res.json());
  }
  /** */
  public uploadWxMoreFile(serverIds: string[]): Promise<any> {
    let observabels: any[] = [];
    observabels.forEach((value: string, i, array) => {
      let observabel = this.uploadWxFile(value);
      observabels.push(observabel);
    });
    return Promise.all(observabels).then(res => {
      let result: any[] = [];
      result.forEach(value => {
        var msg = JSON.parse(value.response);
        if (msg.Code == "1") {
          result.push(msg.Msg);
        }
      });
    });
  }

  private static buildURLSearchParams(paramMap): URLSearchParams {
    let params = new URLSearchParams();
    if (!paramMap) {
      return params;
    }
    for (let key in paramMap) {
      let val = paramMap[key];
      if (val instanceof Date) {
        val = Utils.dateFormat(val, 'yyyy-MM-dd hh:mm:ss')
      }
      params.set(key, val);
    }
    return params;
  }
  private static replaceUrl(url) {
    if (url.indexOf('http://') == -1) {
      url = APP_SERVE_URL + url;
    }
    return 'http://' + url.substring(7).replace(/\/\//g, '/');
  }



  public log(page,msg)
  {
      console.log( Utils.dateFormat(new Date(), 'yyyy-MM-dd hh:mm:ss fff')+">"+ page+">"+msg);
  }


  isWeiXinPub() {
    //window.navigator.userAgent
    var ua: any
    ua = window.navigator.userAgent.toLowerCase();
    //uaMicroMessenger
    if (ua.match(/MicroMessenger/i) == 'micromessenger') {
      return true;
    } else {
      return false;
    }
  }
}



